export default {
	data() {
		return {
			windowHeight: '',
			checkoff:false
		};
	},
	onLoad() {
		var that = this;
		that.windowHeight = this.statusBarHeight + 44;

		uni.getStorage({
			key: 'userinfo',
			success: function(res) {
				that.id = res.data.id;
				that.getinfo();
			}
		})
	},
	methods: {
		back: function() {
			uni.navigateBack({
				delta: 1
			});
		},
		getinfo: function() {
			let that=this;
			uni.request({
				url: that.serveipd+"",
				method: 'GET',
				data: {},
				success: res => {},
				fail: () => {},
				complete: () => {}
			});

		},
		edit:function(){
			this.checkoff=true;
		}
	},
}
